import "./style.css";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import * as dat from "dat.gui";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { Vector2, Vector3 } from "three";
import LocomotiveScroll from "locomotive-scroll";
import CANNON, { Vec3 } from "cannon";

const loadingManager = new THREE.LoadingManager();

loadingManager.onProgress = function (url, itemsLoaded, itemsTotal) {
  console.log(
    "Started loading file: " +
      url +
      ".\nLoaded " +
      itemsLoaded +
      " of " +
      itemsTotal +
      " files."
  );
  let progressBar = document.querySelector(".progress_bar_internal");
  progressBar.style.width = (itemsLoaded / itemsTotal) * 196 + "px";
};

loadingManager.onError = function (url) {
  console.log("error occured " + url);
};
window.scrollTo(0, 0);

gsap.registerPlugin(CSS, CSSRule, ScrollTrigger);

const GLTFloader = new GLTFLoader(loadingManager);
let satelliteMesh = null;
let satellite2 = null;
let satellite3 = null;
//Textures
const textureLoader = new THREE.TextureLoader(loadingManager);
const colorTexture = textureLoader.load(
  "./textures/realEarthMap/1_earth_8k (2).jpg"
);
const nightColorTexture = textureLoader.load(
  "./textures/realEarthMap/2k_earth_nightmap.jpg"
);

const particleTexture = textureLoader.load("./textures/particles/star.png");
// Debug
// const gui = new dat.GUI();
//button
const button = document.querySelector(".button");
// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();
//models
const earthGroup = new THREE.Group();
earthGroup.position.x = 0.9;
earthGroup.position.y = 0.3;
earthGroup.scale.set(0.8, 0.8, 0.8);
// earthGroup.rotation.z = 0.3;

GLTFloader.load(
  // resource URL
  "./models/satelliteModel/satellite.gltf",
  // called when the resource is loaded
  function (gltf) {
    earthGroup.add(gltf.scene);
    satelliteMesh = gltf.scene;
    satelliteMesh.scale.set(0.001, 0.001, 0.001);
    // satelliteMesh.children[0].receiveShadow = true;
    satellite2 = satelliteMesh.clone();
    earthGroup.add(satellite2);
    satellite3 = satelliteMesh.clone();
    satellite3.position.y = 0.5;
    satelliteMesh.position.y = -0.1;
    earthGroup.add(satellite3);
  }
);

// Objects
const earthGeometry = new THREE.SphereBufferGeometry(0.7, 256, 256);

// Materials
const vs = `
varying vec2 vUv;
varying vec3 vNormal;

void main() {
  vUv = uv;
  vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
  vNormal = normalMatrix * normal;
  gl_Position = projectionMatrix * mvPosition;
}
`;

const fs = `
uniform sampler2D dayTexture;
uniform sampler2D nightTexture;

uniform vec3 sunDirection;

varying vec2 vUv;
varying vec3 vNormal;

void main( void ) {
  vec3 dayColor = texture2D( dayTexture, vUv ).rgb;
  vec3 nightColor = texture2D( nightTexture, vUv ).rgb;

  // compute cosine sun to normal so -1 is away from sun and +1 is toward sun.
  float cosineAngleSunToNormal = dot(normalize(vNormal), sunDirection);

  // sharpen the edge beween the transition
  cosineAngleSunToNormal = clamp( cosineAngleSunToNormal * 10.0, -1.0, 1.0);

  // convert to 0 to 1 for mixing
  float mixAmount = cosineAngleSunToNormal * 0.5 + 0.5;

  // Select day or night texture based on mix.
  vec3 color = mix( nightColor, dayColor, mixAmount );

  gl_FragColor = vec4( color, 1.0 );
}

`;

const uniforms = {
  sunDirection: { value: new THREE.Vector3(-1, 0, 0) },
  dayTexture: { value: colorTexture },
  nightTexture: {
    value: nightColorTexture,
  },
};
const earthMaterial = new THREE.ShaderMaterial({
  uniforms: uniforms,
  vertexShader: vs,
  fragmentShader: fs,
});

// Mesh
const earth = new THREE.Mesh(earthGeometry, earthMaterial);
// earth.castShadow = true;
earthGroup.add(earth);

let gravity = document.querySelector(".gravity");
let gravityLine = document.querySelector(".gravity_line");
//Physics
const physicsWorld = new CANNON.World();
let isPhysicsActivated = false;

physicsWorld.gravity.set(0, -9.82, 0);
const physicsEarthShape = new CANNON.Sphere(0.7);

const physicsFloorShape = new CANNON.Plane();
const physicsFloorBody = new CANNON.Body({
  mass: 0,
  shape: physicsFloorShape,
  position: new CANNON.Vec3(0, -0.83, 0),
});
physicsFloorBody.quaternion.setFromAxisAngle(
  new CANNON.Vec3(-1, 0.001, 0),
  Math.PI * 0.5
);
const physicsEarthBody = new CANNON.Body({
  mass: 10000,
  position: new CANNON.Vec3(0, 0, 0),
  shape: physicsEarthShape,
});

const physicsInitiate = function physicsInitiate() {
  if (isPhysicsActivated == false) {
    earthGroup.remove(satelliteMesh);
    earthGroup.remove(satellite2);
    earthGroup.remove(satellite3);

    isPhysicsActivated = true;
    physicsEarthBody.position = new CANNON.Vec3(0, 0, 0);
    physicsEarthBody.quaternion.copy(earth.quaternion);
    gravityLine.classList.add("gravityHovered");

    physicsWorld.addBody(physicsFloorBody);
    physicsWorld.addBody(physicsEarthBody);
  }
};
document.querySelector(".gravity").addEventListener("click", physicsInitiate);
document
  .querySelector(".gravity_line")
  .addEventListener("click", physicsInitiate);

//Particles
const particlesGeometry = new THREE.BufferGeometry();
const particlesCount = 3000;

const particlePositions = new Float32Array(particlesCount * 3);

for (let i = 0; i < particlesCount * 3; i++) {
  if (i % 3 === 1) {
    particlePositions[i] = (Math.random() - 0.5) * 15;
  } else if (i % 3 === 2) {
    particlePositions[i] = (Math.random() - 0.5) * 19;
  } else {
    particlePositions[i] = (Math.random() - 0.5) * 40;
  }
}

particlesGeometry.setAttribute(
  "position",
  new THREE.BufferAttribute(particlePositions, 3)
);

//Particle Material
const particlesMaterial = new THREE.PointsMaterial();
particlesMaterial.size = 0.15;
particlesMaterial.sizeAttenuation = true;
particlesMaterial.color = new THREE.Color("#fffadb");
particlesMaterial.map = particleTexture;
particlesMaterial.alphaMap = particleTexture;
particlesMaterial.transparent = true;

// Points
const particles = new THREE.Points(particlesGeometry, particlesMaterial);
particles.position.z = -12;
scene.add(particles);
scene.add(earthGroup);
// Lights
const ambientLight = new THREE.AmbientLight(0xffffff, 0.1);
scene.add(ambientLight);

const DirectionalLight = new THREE.DirectionalLight(0xffffff, 1.3);
DirectionalLight.position
  .set(earthGroup.position.x - 2, earthGroup.position.y, 0)
  .normalize();
// DirectionalLight.target = earth;
// DirectionalLight.castShadow = true;
scene.add(DirectionalLight);

/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight * 2,
};
window.addEventListener("resize", () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(
  55,
  sizes.width / sizes.height,
  0.1,
  100
);
camera.position.x = 0;
camera.position.y = 0;
camera.position.z = 2;
// camera.lookAt(earth);
scene.add(camera);

//mouse movement

window.addEventListener("mousemove", (e) => {
  if (isPhysicsActivated == false) {
    let mouseX = (e.clientX / window.innerWidth - 0.5) * 0.05;
    let mouseY = -((e.clientY / window.innerHeight - 0.5) * 0.05);
    camera.position.x = mouseX;
    camera.position.y = mouseY;
  }
});
// Controls
// const controls = new OrbitControls(camera, canvas);
// controls.enableDamping = true;

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  antialias: true,
  alpha: true,
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
renderer.setClearColor(0xffffff, 0);
// renderer.shadowMap.enabled = true;
// renderer.shadowMap.type = THREE.PCFSoftShadowMap;

/**
 * Animate
 */
loadingManager.onLoad = function () {
  document.querySelector(".globe_container").classList.add("disppear");
  console.log("loading is supposed to be completed");
};

// document.querySelector("html").style.overflowY = "visible";
const locoScroll = new LocomotiveScroll({
  el: document.querySelector(".data-scroll-container"),
  smooth: true,
});

//scrollTrigger
locoScroll.on("scroll", ScrollTrigger.update);

ScrollTrigger.scrollerProxy(".data-scroll-container", {
  scrollTop(value) {
    return arguments.length
      ? locoScroll.scrollTo(value, 0, 0)
      : locoScroll.scroll.instance.scroll.y;
  },
  getBoundingClientRect() {
    return {
      top: 0,
      left: 0,
      width: window.innerWidth,
      height: window.innerHeight,
    };
  },
});
if (isPhysicsActivated == false) {
  gsap.to(earthGroup.position, {
    x: -0.9,
    y: -0.3,
    delay: 0,
    scrollTrigger: {
      trigger: ".section-1",
      scroller: ".data-scroll-container",
      start: function () {
        let navHeight = document.querySelector("nav").style.height;
        return -navHeight;
      },
      scrub: 0.1,
    },
  });
}

gsap.to(".homeText1", {
  x: -0.9,
  opacity: 0,
  scrollTrigger: {
    trigger: ".section-2",
    end: "-500vh",
    scroller: ".data-scroll-container",
    scrub: 0.2,
  },
});
gsap.from(".homeText2", {
  x: 0.9,
  opacity: 0,
  scrollTrigger: {
    trigger: ".section-2",
    start: "-500vh",
    scroller: ".data-scroll-container",
    scrub: 0.1,
  },
});

gravity.addEventListener("mouseover", () => {
  gravityLine.classList.add("gravityHovered");
  gravityLine.classList.remove("gravityUnHovered");
});

gravity.addEventListener("mouseleave", () => {
  if (isPhysicsActivated == false) {
    gravityLine.classList.add("gravityUnHovered");
    gravityLine.classList.remove("gravityHovered");
  }
});
gravityLine.addEventListener("mouseover", () => {
  gravityLine.classList.add("gravityHovered");
  gravityLine.classList.remove("gravityUnHovered");
});
gravityLine.addEventListener("mouseleave", () => {
  if (isPhysicsActivated == false) {
    gravityLine.classList.add("gravityUnHovered");
    gravityLine.classList.remove("gravityHovered");
  }
});

DirectionalLight.position.set(
  earthGroup.position.x - 2,
  earthGroup.position.y,
  0
);

const clock = new THREE.Clock();
let oldElapsedTime = 0;
let isEarthOutOfView = false;
const tick = () => {
  const elapsedTime = clock.getElapsedTime();

  if (isPhysicsActivated == true) {
    if (isEarthOutOfView == false) {
      if (earth.position.x > 5) {
        scene.remove(earthGroup);
        physicsWorld.removeBody(physicsEarthBody, physicsFloorBody);
        isEarthOutOfView = true;
      }
    }
  }
  // Update objects
  if (isPhysicsActivated == false) {
    earth.rotation.y = elapsedTime * 0.15 + 2;
  }

  if (satelliteMesh !== null) {
    // satelliteMesh.children[0].rotation.y = 0.1 * elapsedTime;
    // satelliteMesh.children[0].rotation.x = 0.1 * elapsedTime;
    // satelliteMesh.children[0].lookAt(earth.position);
    satelliteMesh.position.x = Math.sin(elapsedTime * 0.75);
    satelliteMesh.position.z = Math.cos(elapsedTime * 0.75);
  }

  if (satellite2 !== null) {
    satellite2.position.x = Math.sin(elapsedTime * 0.5) * 0.88;
    satellite2.position.y = Math.cos(elapsedTime * 0.5) * 0.88;
  }

  if (satellite3 !== null) {
    satellite3.position.x = Math.sin(elapsedTime * 1);
    satellite3.position.z = Math.cos(elapsedTime * 1);
  }
  const deltaTime = elapsedTime - oldElapsedTime;
  oldElapsedTime = elapsedTime;
  // physics
  if (isPhysicsActivated == true) {
    // physicsEarthBody.applyForce(
    //   new CANNON.Vec3(-0.02, 0, 0),
    //   physicsEarthBody.position
    // );

    physicsWorld.step(1 / 60, deltaTime, 3);
    earth.position.copy(physicsEarthBody.position);
    let earthQuaternion = earth.quaternion.y;
    earth.quaternion.copy(physicsEarthBody.quaternion);
  }

  // Update Orbital Controls
  // controls.update();

  // Render
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();
